// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Lotterywall from './lotterywall.vue'
import router from './router'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { Lotterywall },
  template: '<Lotterywall/>'
})
Vue.prototype.proxy ='/campusweixin/api/';
Vue.prototype.pc_proxy = '/campusweixin/pc/'
Vue.prototype.getQueryParams = function (name, url) {
  if (!url) url = location.href
  name = name.replace(/[\[]/, "\\\[").replace(/[\]]/, "\\\]");
  var regexS = "[\\?&]" + name + "=([^&#]*)";
  var regex = new RegExp(regexS);
  var results = regex.exec(url);
  return results == null ? null : results[1];
}



