import Vue from 'vue'
import Router from 'vue-router'
const  LotteryWall = r => require.ensure([], ()=>r(require('@/components/shangqiang/LotteryWall.vue')))
Vue.use(Router)
export default new Router({
  routes: [
    {
      path:'/',
      name:'LotteryWall',
      component:LotteryWall
    }
  ]
})



